@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Montserrat:wght@300;400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.main{padding:20px 0;background-color:#fff;min-height:calc(100vh - 230px)}

.homepage{padding-bottom:50px}.homepage__wrap{display:flex;flex-direction:column;justify-content:space-between;align-items:center}@media (min-width: 768px){.homepage__wrap{justify-content:space-between;flex-direction:row}}.homepage__text{padding-bottom:40px;text-align:center}@media (min-width: 1024px){.homepage__text{text-align:left}}.homepage__title{font-size:40px;font-family:'Josefin Sans', sans-serif;margin-bottom:40px}@media (min-width: 768px){.homepage__title{font-size:50px}}@media (min-width: 1024px){.homepage__title{font-size:60px}}@media (min-width: 1440px){.homepage__title{font-size:60px}}.homepage__description{font-family:'Montserrat', sans-serif;font-size:20px;margin-bottom:20px}@media (min-width: 1024px){.homepage__description{max-width:calc(90%)}}.homepage__link{display:block;padding:20px;height:20px;width:77px;margin:0 auto;text-decoration:none;border-radius:10%;border:2px solid;font-family:'Montserrat', sans-serif;color:#000;font-size:20px;transition:all 0.3s}.homepage__link:hover{-webkit-transform:scale(1.1);transform:scale(1.1);background-color:#D90429;color:#fff}

.slider-component{display:block;width:300px;z-index:1}@media (min-width: 768px){.slider-component{width:450px}}@media (min-width: 1024px){.slider-component{width:600px}}.slider-component__img{display:block;width:100%;height:300px;object-fit:contain;z-index:100;transition:all 0.3s}.slider-component__img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}@media (min-width: 768px){.slider-component__img{height:450px}}@media (min-width: 1024px){.slider-component__img{height:600px}}

.in-vino{position:relative;display:flex;align-items:center;justify-content:space-around;height:50vh;width:100%;background-image:url(/front-wine/static/media/1.6a0430af.jpg);background-position:center;background-repeat:no-repeat;background-size:cover}.in-vino::before{content:'';position:absolute;z-index:5;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,0.686)}.in-vino__title{display:block;width:-webkit-max-content;width:max-content;z-index:100;font-size:40px;font-family:'Josefin Sans', sans-serif;text-align:center;align-items:center;line-height:100%;color:#ffffff}@media (min-width: 768px){.in-vino__title{font-size:50px}}@media (min-width: 1024px){.in-vino__title{font-size:60px}}@media (min-width: 1440px){.in-vino__title{font-size:70px}}

.container-wrap{padding:0 10px;max-width:1020px;margin:0 auto}@media (min-width: 768px){.container-wrap{padding:0 20px}}

.category{padding:20px 0 20px;display:flex;flex-direction:column}@media (min-width: 768px){.category{padding:30px 0 30px}}@media (min-width: 1024px){.category{padding:40px 0 40px}}.category__title{display:block;font-family:'Josefin Sans', sans-serif;margin:0 auto 20px;font-size:40px}@media (min-width: 768px){.category__title{font-size:50px;margin:0 auto 30px}}@media (min-width: 1024px){.category__title{font-size:60px;margin:0 auto 40px}}@media (min-width: 1440px){.category__title{font-size:70px;margin:0 auto 50px}}.category__wrap{display:flex;flex-direction:column;justify-content:space-between;margin:0 auto;grid-gap:20px;gap:20px}@media (min-width: 768px){.category__wrap{flex-direction:row}}.category-card{height:300px;width:300px;transition:all 0.3s}@media (min-width: 768px){.category-card{height:200px;width:200px}}@media (min-width: 1440px){.category-card{height:300px;width:300px}}.category-card__img{height:calc(100% - 20px);width:100%;object-fit:cover}.category-card__title{font-family:'Montserrat', sans-serif;font-size:20px;line-height:20px;text-align:center;color:#000}@media (min-width: 768px){.category-card__title{font-size:25px}}@media (min-width: 1440px){.category-card__title{font-size:30px}}.category-card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.winelist__title{font-size:40px;font-family:'Josefin Sans', sans-serif;text-align:center}.winelist__wrap{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}.winelist__card{width:calc(100% - 20px);height:300px;justify-self:center;margin:0 20px 40px 0}.winelist__card:nth-child(3n){margin-right:0}

.wine-card{position:relative;width:100%;border:1px solid #000;padding:10px;box-sizing:border-box;font-family:'Montserrat', sans-serif;color:#000}.wine-card__wrap{display:flex;justify-content:space-between;grid-gap:10px;gap:10px}.wine-card__right{display:inline-block;width:50%}.wine-card__img{display:block;height:200px;width:150px;object-fit:contain;background-color:#2B2D42}.wine-card__title{padding:10px 0px;font-family:'Josefin Sans', sans-serif;font-size:30px}.wine-card__price{text-align:end}.wine-card__price--number{font-size:30px}.wine-card__volume{text-align:end}.wine-card__volume--number{font-size:20px}.wine-card__buy{cursor:pointer;margin-top:170px;height:40px;width:100%;background-color:#2B2D42;color:#fff;border-radius:5%;transition:all 0.3s;text-transform:uppercase}.wine-card__buy:hover{background-color:#EF233C;border-color:#EF233C}


.header{position:-webkit-sticky;position:sticky;top:0;display:flex;align-items:center;justify-content:space-between;width:100%;border-bottom:1px solid black;overflow:hidden;z-index:1000;background-color:#8D99AE}.header__logo{margin-left:34px;height:40px;width:40px;transition:all 0.3s}.header__logo:hover{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}@media (min-width: 768px){.header__logo{height:45px;width:45px}}@media (min-width: 1024px){.header__logo{height:50px;width:50px}}.nav__list{height:100%;font-family:'Montserrat', sans-serif;font-size:20px;max-width:1200px;display:flex;flex:row 1;text-decoration:none;list-style:none;grid-gap:50px;gap:50px;position:relative}.nav__link{display:block;margin:auto;box-sizing:border-box;text-align:center;align-items:center;text-decoration:none;color:black;padding:41px 34px;text-transform:uppercase;font-size:9px;font-weight:800;letter-spacing:0.04em;position:relative;border-bottom:3px solid #00000000;transition:all 0.3s ease}@media (min-width: 768px){.nav__link{font-size:11px}}@media (min-width: 1024px){.nav__link{font-size:13px}}.nav__link--active{border-color:#000}

.logo{margin-left:34px;height:40px;width:40px;transition:all 0.3s}.logo:hover{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}@media (min-width: 768px){.logo{height:45px;width:45px}}@media (min-width: 1024px){.logo{height:50px;width:50px}}

.breadcrumbs{padding:0 32px}.breadcrumbs__icon{height:32px;width:32px}

.footer{display:flex;justify-content:space-between;align-items:center;background-color:#8D99AE;padding:10px;border-top:1px solid black;position:relative;bottom:0;font-family:'Montserrat', sans-serif;font-size:10px}@media (min-width: 768px){.footer{padding:15px;font-size:15px}}@media (min-width: 1024px){.footer{padding:20px}}.footer__logo,.footer__button{display:flex;justify-content:center;align-items:center;cursor:pointer;padding:10px;border:1px solid #ccc;border-radius:5px}.footer__links{display:flex;justify-content:space-between;grid-gap:20px;gap:20px}@media (min-width: 768px){.footer__links{grid-gap:30px;gap:30px}}@media (min-width: 1024px){.footer__links{grid-gap:40px;gap:40px}}.footer__links-link{text-decoration:none;color:#000;cursor:pointer}

